function sliderPartners() {
    var swiper = new Swiper('.partners .swiper', {
        slidesPerView: '6',
        spaceBetween: 30,
        navigation: {
            nextEl: '.partners .swiper-button-next',
            prevEl: '.partners .swiper-button-prev',
        },
        pagination: {
            el: ".partners .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: '1',
                spaceBetween: 20,
            },
            375: {
                slidesPerView: '2',
                spaceBetween: 20,
            },
            741: {
                slidesPerView: '4',
                spaceBetween: 20,
            },
            941: {
                slidesPerView: '5',
                spaceBetween: 20,
            },
            1201: {
                slidesPerView: '6',
                spaceBetween: 30,
            }
        }
    })
}


$(document).ready(function() {

    sliderMain();
    sliderMaterials();
    sliderAdvantages();
    sliderAbout();
    sliderObjects();
    sliderRent();
    sliderPartners();


    // Burger - Menu
    $('.header-burger').click(function () {
        $(this).toggleClass('header-burger--active')
        $('.header-bottom').toggleClass('header-bottom--active')
        $('body').toggleClass('fixed-body')
    })

    // Hover-Click
    $(".header-bottom__item").hover(onIn, onOut);
    $(".header-bottom__item .header-bottom__item-show").click(function() {
        if ($(this).parents(".header-bottom__item").hasClass("header-bottom__item--active")) {
            $(this).parents(".header-bottom__item").removeClass("header-bottom__item--active")
        } else {
            $(this).parents(".header-bottom__item").addClass("header-bottom__item--active")
        }
    })

    // Search
    $('.sidebar-search').click(function () {
        $('.search').addClass('search--active')
    })

    $('.search-close').click(function () {
        $('.search').removeClass('search--active')
    })

    // Search - Menu
    $('.sidebar-burger').click(function () {
        $('.sidebar-menu').addClass('sidebar-menu--active')
    })

    $('.sidebar-menu__close').click(function () {
        $('.sidebar-menu').removeClass('sidebar-menu--active')
    })

    // Accordion
    if (window.matchMedia("(max-width: 576px)").matches) {
        $(".footer__item-show").click((function(){
        $(this).toggleClass("footer__item-show--active"),
        $(this).siblings(".footer__item-hidden").slideToggle()}));
    }

    // Calc
    function cartCalc(){
        $('.cartcalc .ccalc-minus').click(function(){
            let a = $(this).closest('.cartcalc').find('input').val();
            if(a>1){
                let b = +a-1;
                $(this).closest('.cartcalc').find('input').val(b);
            }else{
                $(this).closest('.cartcalc').find('input').val(a);
            }
        });
        $('.cartcalc .ccalc-plus').click(function(){
            let a = $(this).closest('.cartcalc').find('input').val();
            let b = +a+1;
            $(this).closest('.cartcalc').find('input').val(b);
        });
    }
    cartCalc()

    // InputMask
    $(document).ready(function() {
        $(".input-tel").mask("+7 999 999 99-99");
    });

    // Select
    $(".custom_select select").niceSelect()

    // Cartcalc - Click
    $('.ccalc-click').click(function () {
        $(this).siblings('.ccalc-input').css({'color': '#ffffff'})
        $(this).siblings('.input-placeholder').addClass('input-placeholder--active')
    })

})

function onIn() {
    if (window.innerWidth > 740) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("header-bottom__item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 740) {
        $(this).removeClass("header-bottom__item--active")
    }
}